.chat {
  min-width: 300px;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #242526;
  border: 3px solid #242425;
  border-radius: 15px 15px 0 0;
  overflow: hidden;
}

.chat-small{
  width: 50px;
  height: 50px;
}

.chat-head {
  padding: .7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #161617;
  color: #ffffff;
  cursor: pointer;
}

.chat-head-small{
  height:50px;
  overflow: hidden;
  width: 1000px;

}
.chat-head-image-container {
  height: 25px;
  width: 25px;
  overflow: hidden;
}

.logoAndTitle {
  display: flex;
  gap: .5rem;
  align-items: center;
}

.chat-head-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.messages-container {
  background-color: #242526;
  height: 350px;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  overflow: scroll;
}

.messages-container > :first-child {
  margin-top: auto;
}

form {
  display: flex;
  padding: .3rem;
}

form input {
  width: 100%;
  background-color: #495252;
  color: white;
  padding: 1rem;
  border-radius: 15px;
  border: none;
  font-size: inherit;
}

form button {
  background-color: #242526;
  cursor: pointer;
  color: #cdfbfa;
  border: none;
  padding: .5rem;
  font-weight: bold;
  border-radius: 15px;
}

.close-btn{
  width: 2rem;
  height: 2rem;
  color: #ffffff55;
  font-weight: bold;
  border-radius: 50px;
  cursor: pointer;
  background-color: #242526;
  border: none;
}

.button-wrapper{ 
  display: flex;
  gap: .5rem;
}

h2::first-letter {
  text-transform:capitalize;
}


.hide-body {
  display: none;
}

.chat-full-screen{
  width: 100%;
  height: 100%;
  border-radius: 0;
}

.messages-container-full {
  height: 74vh;
}
